import React, {ChangeEvent, useCallback, useMemo, useState} from 'react';

import {Container, Typography} from '@mui/material';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import {Link, PageProps, graphql} from 'gatsby';

import Layout from '../components/layout';
import {CollegeDetailsProps} from '../templates/college-details';



type DataProps = {
  allPassPercentage2022Json: {
    nodes: any[];
  };
  allStrapiCollege: {
    nodes: CollegeDetailsProps[];
  };
};

const TopCollegesVikatanSurvey = ({
  data: {allPassPercentage2022Json, allStrapiCollege},
}: PageProps<DataProps>) => {

  const [searchText, setSearchText] = useState('');
  const allColleges = useMemo(() => allPassPercentage2022Json.nodes, [allPassPercentage2022Json]);
  // console.log('allColleges : ', allColleges);

  const allCollegeDetails = useMemo(() => allStrapiCollege.nodes, [allStrapiCollege]);
  console.log('allCollegeDetails : ', allCollegeDetails);
  
  const handleChange = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      setSearchText(event.target.value);
    },
    [setSearchText]
  );

  const filteredColleges = useMemo(() => {
    // filter branch by name or code
    return allColleges.filter((college) => {
      return (
        college.name.toLowerCase().includes(searchText.toLowerCase()) ||
        college.ccode.toString().includes(searchText) ||
        college.district.toString().includes(searchText)
      );
    });
  }, [allColleges, searchText]);

  const getCollegeCityByCode = useCallback((ccode: number) => {
    const college = allCollegeDetails?.find(({code}) => ccode.toString() === code);
    return college?.city || '';
  }, [allCollegeDetails]);
  


  return (
    <Layout headerTitle={'Colleges'} backLink="/" shouldGoBack>
      <Container sx={{my: 4}}>
        <Typography variant="h5" component="div" color="secondary" sx={{mb: 2}}>
          {'பி.இ./பி.டெக். பட்டப்படிப்புகள் தன்னாட்சி அல்லாத அண்ணா பல்கலைக்கழக கல்லூரிகளின் கல்வித் திறனாய்வு, ஏப்ரல் / மே 2022 தேர்வுகளின் படி'}
        </Typography>
        <Box sx={{mb: 2}}>
          <TextField id="standard-search"
            label="Search by College name, college code or district"
            placeholder='Search by College name, college code or district'
            type="search"
            variant="filled"
            onChange={handleChange}
            fullWidth/>
        </Box>
        {filteredColleges.map((college, index) => {
          const collegeCity = getCollegeCityByCode(college.ccode);
          const district = college.district;
          return (
            <Card key={college.id} sx={{mb: 2, p: 2}} variant="outlined">
              <Typography sx={{mb: 2}}>
                {index + 1}. {college.name} (Code: {college.ccode}){', '}
                {district !== collegeCity ? `${collegeCity}, ${district}` : district}
              </Typography>
              <Typography sx={{my: 1}}>
            தேர்வு எழுதிய மாணவர்கள்: {college.studentsCount}
              </Typography>
              <Typography sx={{my: 1}}>
            தோல்வி அடைந்த மாணவர்களின் எண்ணிக்கை: <Chip label={college.studentsCount - college.passCount} color="error" size="small" variant="outlined" />
              </Typography>
              <Typography sx={{my: 1}}>
            தேர்ச்சி பெற்ற மாணவர்களின் எண்ணிக்கை: <Chip label={college.passCount} color="success" size="small" variant="outlined" />
              </Typography>
              <Typography sx={{my: 1}}>
            தேர்ச்சி சதவீதம்: {college.percentage}
              </Typography>
              <Stack direction="row"
                justifyContent="flex-end"
                alignItems="center"
                spacing={2}>
                <Button size="small" component={Link} to={`/${college.ccode}`}>
              View College
                </Button>
              </Stack>
            </Card>
          );
        })}
      </Container>
    </Layout>
  );
};


export const query = graphql`
  query {
    allPassPercentage2022Json {
      nodes {
        ccode
        district
        id
        name
        passCount
        percentage
        studentsCount
      }
    }
    allStrapiCollege(sort: {name: ASC}) {
      nodes {
        name
        code
        city
        id
        latitude
        longitude
        address
        branches {
          name
          code
          id
        }
        college_type {
          name
          id
        }
      }
    }
  }
`;

export default TopCollegesVikatanSurvey;

